import * as React from 'react';
import { DataGrid } from '@material-ui/data-grid';
import { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { DeleteOutline } from '@material-ui/icons';
import { Link } from 'react-router-dom';
import { Button } from '@material-ui/core';
import { productRows } from './productsGoods';

const useStyle = makeStyles((theme) => ({
  btn:{ width:"32px",
        height:"32px", 
        borderRadius:"50%", 
        objectFit:"cover", 
        marginRight:"10px",

      },
  deleteBtn:{
        color:"red", 
        marginLeft:"15px"
  }
}))

export default function Products() {
  const [data, setData] = useState(productRows);

  const productCol = [
    { field: 'id', headerName: 'ID', width: 90 },
    {
      field: 'name',
      headerName: 'Product Name',
      width: 200,
      renderCell: (param) =>{
        return(
          <div style={{display:"flex", alignItems:"center"}}>
            <img style={{width:"32px",height:"32px", borderRadius:"50%", objectFit:"cover", marginRight:"10px" }} src={param.row.avatar} alt="Dashboard avatar" />
            {param.row.name}
          </div>
        )
      }
    },
    {
      field: 'status',
      headerName: 'Status',
      width: 150,
      editable: true,
    },
    {
      field: 'Price',
      headerName: 'Price',
      width: 230,
      editable: true,
    },
    {
      field: 'Action',
      headerName: 'Edit',
      width: 150,
      renderCell: (param) =>{
        return(
          <div style={{display:"flex", alignItems:"center"}}>
              <Link to={"/product/" + param.row.id}>
                <Button className={classes.btn}>Edit</Button>
              </Link>
            <DeleteOutline  onClick={() => handleDelete(param.row.id)} className={classes.deleteBtn} />
          </div>
        )
      }
    },
  ] ;  
  const handleDelete = (id) => {
    setData(data.filter((item) => item.id !== id));
  };
  
  const classes = useStyle()
  return (
    <div style={{height:550, width:'100%'}}>
      <DataGrid
        rows={productRows}
        columns={productCol}
        pageSize={8}
        checkboxSelection
        disableSelectionOnClick
      />
    </div>
  );
}

