import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { AppBar, Avatar, Badge, Button, Toolbar, Typography } from '@material-ui/core';
import MailIcon from '@material-ui/icons/Mail';
import { Notifications } from '@material-ui/icons';
import DehazeIcon from '@material-ui/icons/Dehaze';
import SettingsIcon from '@material-ui/icons/Settings';



const useStyle = makeStyles((theme) => ({
    toolbar:{
        display: "flex",
        justifyContent: "space-between",
        backgroundColor: "#deecff",
        color:"#333"
    },
    navLg:{
        display: "block",
        [theme.breakpoints.down('sm')]: {
           display: "none",
      },
    },
    navSm:{
        display: "none",
       [theme.breakpoints.down('sm')]: {
       display: "block",
        },
      },
    icon:{
        alignItems: "center",
        display: "flex",
        [theme.breakpoints.down('sm')]: {
            display: "none",
        },
    },
badge:{
        marginRight: theme.spacing(2),
    },
dehazeicon:{
    display: "none",
       [theme.breakpoints.down('sm')]: {
       display: "block",
       color:"white"
    },
},

}));

const NavBar = () => {
    const [open, setOpen] = useState(false);
    const classes = useStyle({ open });
    return(
        <>
        <div>
            <AppBar>
               <Toolbar className={classes.toolbar}>
                   <Typography variant="h6" className={classes.navLg}>
                       Bug Dashboard
                   </Typography>
                   <Typography  variant="h6" className={classes.navSm}>
                       Dashboard
                   </Typography>
                   <div className={classes.icon}>
                    <Badge badgeContent={4} color="secondary" className={classes.badge}>
                      <MailIcon />
                    </Badge>
                    <Badge badgeContent={4} color="secondary" className={classes.badge}>
                      <Notifications />
                    </Badge>
                    <SettingsIcon className={classes.badge}/>
                    <Avatar className={classes.orange}>AA</Avatar>
                    </div>
                    <Button className={classes.dehazeicon}>
                        <DehazeIcon onClick={() => setOpen(true)} />
                    </Button>
               </Toolbar>
            </AppBar>
        </div>
        </>
    )
}

export default NavBar