import { FormControl, FormControlLabel, FormLabel, Grid, makeStyles, Radio, RadioGroup, TextField, Typography } from '@material-ui/core'
import React from 'react'
const useStyles = makeStyles((theme) => ({
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
        [theme.breakpoints.down('sm')]:{
            display: "flex",
            width: '100%',
        }
      },
      textField:{
        //   padding:"10px",
        width:"80%",
        margin:"10px",
        // marginTop:"10px"


      },
    //   title:{
    //    padding:"10px",
    //   },
}))

export default function UserForm() {
    const classes = useStyles()
    return (
        <div>
            <div>
                        <Typography variant="h4">
                        New User
                        </Typography>
                            <form className={classes.form} noValidate>
                                <Grid container >
                                  <Grid item xs={12} spacing={2} >
                                      <span>
                                          <Typography>
                                              Username
                                          </Typography>
                                      </span>
                                    <TextField
                                        variant="outlined"
                                        className={classes.textField}
                                        required
                                        id="userName"
                                        label="Username"
                                        name="Username"
                                        autoComplete="Username"
                                    /> 
                                  </Grid>
                                  <Grid item xs={12} >
                                    <span>
                                          <Typography>
                                              Email
                                          </Typography>
                                    </span>
                                    <TextField
                                        variant="outlined"
                                        required
                                        className={classes.textField}
                                        id="email"
                                        label="Email Address"
                                        name="email"
                                        autoComplete="email"
                                    /> 
                                  </Grid>
                                  <Grid item xs={12} >
                                        <span>
                                          <Typography>
                                            Phone 
                                         </Typography>
                                        </span>
                                    <TextField
                                        variant="outlined"
                                        required
                                        className={classes.textField}
                                        id="email"
                                        label="Email Address"
                                        name="email"
                                        autoComplete="email"
                                    /> 
                                  </Grid>
                                </Grid>
                            </form>

                <FormControl component="fieldset" style={{marginTop:"10px"}}>
                    <FormLabel component="legend">Appearance</FormLabel>
                        <RadioGroup row aria-label="position" name="position" defaultValue="top">
                        <FormControlLabel value="end" 
                        control={<Radio color="primary" />} label="Online" />
                        <FormControlLabel value="end" 
                        control={<Radio color="primary" />} label="Offline" />
                        <FormControlLabel value="end" 
                        control={<Radio color="primary" />} label="Disable" />
                        </RadioGroup>
                 </FormControl>
            </div>
        </div>
    )
}
