import React from 'react';
// import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { Card, CardContent } from '@material-ui/core';
import { ArrowDownward } from '@material-ui/icons';


function preventDefault(event) {
  event.preventDefault();
}

const useStyles = makeStyles((theme)=> ({
    card:{
        width: "80%",
        margin:"0 auto",
        backgroundColor: "#b2ceeb",
      },
      arrow:{
          color: "red",
          '&:hover': {
            color: '#ed9595',
            fontSize: "20px"
          },
    },
    title:{
        fontSize: "20px",
        color: "#2e2d2d",
    },
    totalMoney:{
        fontSize: "30px",
        color: "black",
        paddingRight: theme.spacing(3)
    },
    moneyRate:{
        fontSize: "20px",
        color: "black",
        // padd: theme.spacing(3)
    },
    money:{
        paddingBottom: theme.spacing(1)
    }

}))
  
;


export default function Carte({ rate, change, content}) {
  const classes = useStyles();
  return (
            <Card className={classes.card}> 
                <CardContent>
                    <Typography className={classes.title} color="textSecondary" gutterBottom>
                    {content.title}
                    </Typography>
                    <Typography  className={classes.money}>
                    <span className={classes.totalMoney}>
                        {content.amount}
                    </span>     
                    <span className={classes.moneyRate}>
                        {rate} <ArrowDownward className={classes.arrow} onClick={change}/>
                    </span>
                    </Typography>
                    <Typography className={classes.text} color="textSecondary">
                        {content.text}
                    </Typography>
                </CardContent>
            </Card>
  );
}