import { Grid } from '@material-ui/core';
import React from 'react';
import Navbar from './Components/Navbar/Navbar'
import Sidebar from './Components/Sidebar/Sidebar';
import { makeStyles } from '@material-ui/core/styles';
import Carts from './Components/Deploy/Carts';
import {
  BrowserRouter as Router,
  Routes,
  Route,
} from "react-router-dom";

import UsersTab from './Components/UsersTable/UsersTab';
import Users from './Components/Users/Users';
import NewUser from './Components/Users/NewUser/NewUser';
import Products from './Components/Products/Products';
import ProductSingleShow from './Components/Products/ProductSingleShow/productSingleShow';
import CreateNewProduct from './Components/Products/CreateNewProduct';


function App() {
  const useStyle = makeStyles((theme) => ({
    grid:{
      marginTop: theme.spacing(10),
    }
  }))

const classes = useStyle ()
  return (

    <Router>
      <Navbar />

      <Grid container spacing={1} >
        <Grid item sm={2} xs={2}>
          <Sidebar />
        </Grid>
        <Grid item sm={10} xs={10} className={classes.grid}>
          <Routes>
            <Route exact path="/"  element={<Carts />} />
            <Route path="/Users" element={<UsersTab />} />
            <Route path="/user/:userId" element={<Users/>} />
            <Route path="/newUser" element={<NewUser/>} />
            <Route path="/product" element={<Products/>} />
            <Route path="/product/:productId" element={<ProductSingleShow />} />
            <Route path="/createNewProduct" element={<CreateNewProduct />} />
          </Routes>
          </Grid>
      </Grid>
    </Router>
  );
}

export default App;
