import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Container, Typography } from '@material-ui/core';
// import { AttachMoney, ContactPhone, DynamicFeed, Email, Home, Message, More, PermIdentity, Report, Storefront, Timeline, TrendingUp } from '@material-ui/icons';
import sideBarContents from './sideBarContents';

const useStyle = makeStyles((theme) => ({
    container:{
        paddingLeft: theme.spacing(2),
        paddingTop: theme.spacing(8),
        backgroundColor: "#deecff",
        height: "100%",
        color: "black",
        position: "sticky",
        top:0,
        [theme.breakpoints.down('sm')]:{ 
          backgroundColor: "white",
          color: "#555",
          border: "1px solid  #ece7e7",
          paddingRight: theme.spacing(5),
        }
      },
    categories:{
      paddingTop: theme.spacing(2),
      [theme.breakpoints.down('sm')]:{ 
      }
      
    },
    head:{
      fontSize: "20px",
      color: "#949492",
      [theme.breakpoints.down('sm')]:{ 
        display: "none"
      },
    },
    subItem:{
      display: "flex",
      color:"#3b3b3a",
      '&:hover': {
        color: '#a3a3a2',
        fontSize: "15px"
      },
      pointer: "cursor",
      paddingBottom: theme.spacing(1),

    },
    text:{
      fontSize: "15px",
      [theme.breakpoints.down('sm')]:{ 
        display: "none"
      }
    },
  

}));

const Sidebar = () => {
    const classes = useStyle({});
    return(
      <Container className={classes.container}>
          {sideBarContents.map(sidebarcontent => {
            return(
          <div className={classes.items}>
            <div className={classes.categories}>
                <Typography className={classes.head}>
                    {sidebarcontent.head}
                </Typography>
            <div className={classes.subItem}>
              {sidebarcontent.icon_1}
              <Typography className={classes.text}>
              {sidebarcontent.text_1}
              </Typography>
            </div>
            <div className={classes.subItem}>
              {sidebarcontent.icon_2}
              <Typography className={classes.text}>
              {sidebarcontent.text_2}
              </Typography>
            </div>
            <div className={classes.subItem}>
              {sidebarcontent.icon_3}
              <Typography className={classes.text}>
              {sidebarcontent.text_3}
              </Typography>
            </div>
            </div>
        </div>
            )
          })}
        </Container>
    )
}

export default Sidebar