import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { AttachMoney, ContactPhone, DynamicFeed, Email, Home, Message, More, PermIdentity, Report, Storefront, Timeline, TrendingUp } from "@material-ui/icons";
import { Link } from 'react-router-dom';

const useStyle = makeStyles((theme) => ({
    icon:{
        marginLeft: theme.spacing(1),
        paddingRight: theme.spacing(1),
      },
      sideLink:{
          textDecoration:"none",
      }
}))


const sideBarContents =  [
    {
        head: "Dashboard",
        text_1: <Link to='/'style={{textDecoration:"none", color:"inherit", marginLeft:"5px"}} >Home</Link>,
        icon_1: <Link to='/'style={{textDecoration:"none", color:"inherit", marginLeft:"5px" }}><Home  className={useStyle.icon} /></Link> ,
        text_2: "Analytics",
        icon_2: <Timeline style={{marginRight:"5px"}} className={useStyle.icon} />,
        text_3:"Sales",
        icon_3: <TrendingUp style={{marginRight:"5px"}} className={useStyle.icon}/>,

    },
    {
        head: "Quick Menu",
        text_1: <Link to='/Users'style={{textDecoration:"none", color:"inherit", marginLeft:"5px"}} >User</Link>,
        icon_1: <Link to='/Users'style={{textDecoration:"none", color:"inherit" }} ><PermIdentity className={useStyle.icon} /></Link>,
        text_2: <Link to='/product'style={{textDecoration:"none", color:"inherit" , marginLeft:"5px"}} >Product</Link>,
        icon_2: <Link to='/product'style={{textDecoration:"none", color:"inherit"}}> <Storefront  className={useStyle.icon} /></Link>,
        text_3:"Transaction",
        icon_3: <AttachMoney className={useStyle.icon} />,
    },
    {
        head: "Notification",
        text_1: "Mails",
        icon_1: <Email style={{marginRight:"5px"}} className={useStyle.icon} />,
        text_2: "Feedback",
        icon_2: <DynamicFeed  style={{marginRight:"5px"}}className={useStyle.icon} />,
        text_3:"Messages",
        icon_3: <Message  style={{marginRight:"5px"}} className={useStyle.icon} />,
    },
    {
        head: "Staff",
        text_1: "Contact Us",
        icon_1: <ContactPhone style={{marginRight:"5px"}} className={useStyle.icon} />,
        text_2: "Report",
        icon_2: <Report style={{marginRight:"5px"}}  className={useStyle.icon} />,
        text_3:"Tell Us More",
        icon_3: <More  style={{marginRight:"5px"}} className={useStyle.icon} />,
    },
]

export default sideBarContents