import React, {useState } from 'react'
import Carte from '../Main/Carte/Carte'
import { Grid } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import cardContent from '../Main/cardContent'
import Charts from '../Charts/Charts'
import { data } from '../Charts/chartsData'
import LgWidget from '../Main/Widget/WidgetLg'
import SmWidget from '../Main/Widget/WidgetSm'


const useStyle = makeStyles((theme) => ({
    grid:{
      display: "flex",
        [theme.breakpoints.down("sm")]: {
            display: "flex",
            flexDirection: 'column',
            width: "100%"
        } 
    },
    userGrid:{
      display:"flex",
      width:"100%"
    }
}))


export default function Carts() {
    const classes = useStyle();
    const [figure, setFigure] = useState(2.0) 

    const changeFigure = () => {
      setFigure(Math.floor(Math.random() * 10).toFixed(2)/100)
    }

    return (
 
        <div>
            <Grid container spacing={2} className={classes.grid}>
              {cardContent.map(content => {
                return (
                  <Grid item xs={4}>
                    <Carte content={content} change={changeFigure} rate={figure} />
                  </Grid>
                )
              })}
            </Grid>
            <Charts data={data} dataKey="Active User" title="User Analytics" grid  />
              <div className={classes.userGrid}>
                <SmWidget />
                <LgWidget />
              </div>
        </div>
        
    )
}

