import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import userDatas from './widgetLgContent';
import { Avatar, Card, CardContent, Paper, Typography } from '@material-ui/core';

const useStyles = makeStyles((theme)=>({
  table: {
    minWidth: 570,
  },
  card:{
      marginTop:theme.spacing(3)
  },
  avatar:{
      display:"flex"
  }

}));

export default function LgWidget() {
  const classes = useStyles();

  return (
            <Card className={classes.card}>
                <CardContent>
                {/* <Typography>
                 User Analytics
                    </Typography> */}
                    <TableContainer component={Paper}>
                    <Table className={classes.table} aria-label="simple table">
                        <TableHead>
                        <TableRow>
                            <TableCell align="left">Name</TableCell>
                            <TableCell align="left">Date</TableCell>
                            <TableCell align="left">Amount</TableCell>
                            <TableCell align="left">Status</TableCell>
                        </TableRow>
                        </TableHead>
                        <TableBody>
                        {userDatas.map(userData => (
                            <TableRow key={userData.name}>
                            <span component="th" scope="row" className={classes.avatar}>
                        <TableCell>
                            <Avatar alt="R S" src="https://v4.mui.com/static/images/avatar/1.jpg" />
                        </TableCell>
                                
                                <TableCell>{userData.name}</TableCell>
                            </span>
                            <TableCell align="left">{userData.date}</TableCell>
                            <TableCell align="left">{userData.Amount}</TableCell>
                            <TableCell align="left">{userData.Status}</TableCell>
                            </TableRow>
                        ))}
                        </TableBody>
                    </Table>
                    </TableContainer>
                </CardContent>
            </Card>   
    

  );
}
