import React from 'react'
import { TextField, Grid, Typography, makeStyles } from '@material-ui/core'
import { gender } from '../usersDetails'



const useStyles = makeStyles((theme) => ({
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
        [theme.breakpoints.down('sm')]:{
            display: "flex",
            width: '100%',
        }
      },
      textField:{
        //   padding:"10px",
        width:"80%",
        margin:"10px",
        // marginTop:"10px"


      },
    //   title:{
    //    padding:"10px",
    //   },
}))
export default function PersonalForm() {
    const classes = useStyles()
    return (
        <div>
            <Typography variant="h4">
                        Personal Details
            </Typography>
                        <div > 
                        <form className={classes.form} noValidate>
                                <Grid container >
                                  <Grid item xs={12} spacing={2} >
                                      <span>
                                          <Typography>
                                              Fullname
                                          </Typography>
                                      </span>
                                    <TextField
                                        variant="outlined"
                                        required
                                        className={classes.textField}
                                        id="userName"
                                        label="Fullname"
                                        name="Fullname"
                                        autoComplete="Fullname"
                                    /> 
                                  </Grid>
                                  <Grid item xs={12} >
                                    <span>
                                          <Typography>
                                              Password
                                          </Typography>
                                    </span>
                                    <TextField
                                        variant="outlined"
                                        required
                                        className={classes.textField}
                                        id="password"
                                        label="Password"
                                        name="password"
                                        autoComplete="password"
                                    /> 
                                  </Grid>
                                  <Grid item xs={12} >
                                        <span>
                                          <Typography>
                                            Phone 
                                         </Typography>
                                        </span>
                                    <TextField
                                        variant="outlined"
                                        required
                                        className={classes.textField}
                                        id="number"
                                        label="Phone Number"
                                        name="Phone Number"
                                        autoComplete="Phone Number"
                                    />  
                                    <TextField
                                        id="filled-select-currency-native"
                                        select
                                        label="Gender"
                                        className={classes.textField}
                                        SelectProps={{
                                            native: true,
                                        }}
                                        variant="filled"
                                        >
                                        {gender.map((sex) => (
                                            <option key={sex.value} value={sex.value}>
                                            {sex.label}
                                            </option>
                                        ))}
                                    </TextField>
                                  </Grid>
                                </Grid>
                            </form>
                        </div>
        </div>
    )
}
