import { Avatar, Button, Card, CardContent, List, ListItem, ListItemAvatar, ListItemText, makeStyles, Typography } from '@material-ui/core';
import React from 'react';
import widgetSmContents from './widgetSmContent'
import { Paper } from '@material-ui/core';

const useStyle = makeStyles((theme) => ({
    card:{
        // width: "40%",
        margin: "20px",
        [theme.breakpoints.down('sm')]:{ 
            display: "flex",
            width: "100%"
          }
    },
    col:{
        display: "flex",
        justifyContent:"space-between",
        width:"100%"
     },
    text:{
        display: "flex",
        flexDirection:"column",
        justifyContent:"space-between",
        marginRight: theme.spacing(5),
        width:"100%"
    },
    textsm:{
      fontWeight:"bold",
      fontSize:"15px"
    },
    button:{
        padding: "10px",
        alignItems:"center",
        marginLeft: theme.spacing(5),
        width: "50%"
    },
    
}))

const SmWidget = () =>{
    const classes = useStyle()
    return(
        <div>
            <Card className={classes.card}>
                <CardContent>
                    <Typography color="initial">
                        New Join Member
                    </Typography>
                    {widgetSmContents.map(widgetsmcontent => {
                        return(
                            <List component={Paper}>
                                <ListItem>
                                    <div className={classes.col}>
                                        <ListItemAvatar>
                                            <Avatar>
                                                {widgetsmcontent.avatar}
                                            </Avatar>
                                        </ListItemAvatar>
                                        <div className={classes.text}>
                                            <span className={classes.textsm}>
                                                {widgetsmcontent.name}
                                            </span>
                                            <span style={{width:"100%"}}>
                                                {widgetsmcontent.jobTitle}
                                            </span>
                                        </div>
                                        <Button className={classes.button}variant="outlined">
                                            {widgetsmcontent.icon} Button
                                        </Button>
                                    </div>
                                </ListItem>    
                            </List>
                        )
                    })}
                    
                </CardContent>
            </Card>

        </div>
    )
}

export default SmWidget